import React from 'react';
import { Row, Col, Box} from '@smooth-ui/core-sc';

import * as Styled from './Portfolio.style';
import captions from './captions.json'

function pickRandomQuestion(){
  var obj_keys = Object.keys(captions);
  var ran_key = obj_keys[Math.floor(Math.random() *obj_keys.length)];
  var selected_caption = captions[ran_key];
  var image_id = selected_caption.image_id;
  var caption = selected_caption.caption;

  var state = {image_id: image_id, caption: caption};

  return (
        <Row
          mb={{xs:"30px", sm:"40px", lg:"50px", xl:"80px"}}
        >
          <Col
            xs={12}
            mb={{xs:"15px", xl:"0px"}}
            xl={6}
            display= "flex"
            alignItems= "flex-end"
          >
            <img 
              src={process.env.PUBLIC_URL + '/img/val/' + state.image_id}
              alt={state.caption}
              style={{
                width:"100%",
                display: "block",
                margin: "0 auto",
                borderRadius: "5px"
              }}
            />
          </Col>

          <Col
            xs={20}
            xl={6}
            pl={{xl:"30px"}}
          >
            <Styled.SectionHeading>
              Captions
            </Styled.SectionHeading>
            <Styled.Paragraph>
              {state.caption}
            </Styled.Paragraph>
          </Col>
        </Row>
  );
}

const Portfolio = () => (
  <Row
    mx={0}
    px={0}
    pt={{xs:"120px", sm:"130px", lg:"140px", xl:"155px"}}
    pb="20px"
  >
    <Col
      px={0}
      xs={12}
      sm={10}
      osm={1}
      lg={8}
      olg={2}
      my={0}
      mx="auto"
    >
      <Box
        as="header"
        role="banner"
      >
        <Row
          mb={{xs:"20px", sm:"30px", lg:"40px", xl:"50px"}}
        >
          <Col 
            xs={12}
          >
            <Styled.SectionHeading
              as="h1"
            >
              Image Captioning with Spatial Attention and Concept Augmentation
            </Styled.SectionHeading> 
            <Styled.Paragraph
              email
            >
              Please check out our&nbsp;
              <a 
                href="https://www.mdpi.com/2076-3417/9/9/1886"
              >
                <span
                  className="email-link_content"
                  tabIndex="-1"
                >
                  journal paper
                </span>
              </a> in Applied Sciences (ISSN 2076-3417).
            </Styled.Paragraph>        
          </Col>
        </Row>
      </Box>

      <Box
        as="main"
        role="main"
      >
        {pickRandomQuestion()}
        <Styled.Hr />
        {pickRandomQuestion()}
        <Styled.Hr />
        {pickRandomQuestion()}
        <Styled.Hr />
        {pickRandomQuestion()}
        <Styled.Hr />
        {pickRandomQuestion()}
        <Styled.Hr />
      </Box>
    </Col>
  </Row>
);

export default Portfolio;
